.signature {
  padding: 16px;
  display: flex;
  flex-direction: column;
  background-color: #F2F2F2; }

.signature__paragraph {
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px;
  color: #1B1C21; }

@media (min-width: 768px) {
  .signature {
    flex-direction: row; }
  .signature__paragraph {
    flex-basis: 50%; }
  .signature {
    padding-left: 24px;
    padding-right: 24px; } }

@media (min-width: 1440px) {
  .signature {
    width: 1056px;
    margin: 0 auto;
    padding-left: 32px;
    padding-right: 32px; } }
