.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  top: 0;
  left: 0;
  z-index: 60;
  opacity: 0;
  visibility: hidden;
  transition: all 0.8s ease 0s; }

.popup:target {
  opacity: 1;
  visibility: visible; }

.popup:target .popup__content {
  transform: translate(0px, 0px);
  opacity: 1; }

.popup__area {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }

.popup__body {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 30px 0 30px 10px;
  right: 0;
  top: 0; }

.popup__content {
  background-color: white;
  color: black;
  width: 303px;
  position: absolute;
  height: 107%;
  transition: all 0.8s ease 0s;
  opacity: 0;
  transform: translate(100%, 0px); }

.popup__close {
  position: absolute;
  left: 30px;
  top: 10px;
  font-size: 20px;
  color: black;
  text-decoration: none; }

.popup__title {
  font-family: "TTLakes-Bold", Arial, sans-serif;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.6px;
  color: #1B1C21;
  margin-top: 80px;
  margin-bottom: 24px;
  display: flex;
  align-items: center; }

.popup__title::before {
  content: "";
  width: 3px;
  height: 40px;
  background: #41F6D7;
  border-radius: 2px;
  display: inline-block;
  margin-right: 12px; }

.popup__close:hover {
  transform: scale(1.1); }

@media (min-width: 768px) {
  .popup__close {
    left: -45px; }
  .popup__content {
    width: 400px; } }
