.site-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 10px;
  background-color: white; }

.site-navigation__logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 180px; }

.site-navigation__logo::after {
  content: "";
  width: 2px;
  height: 32px;
  background: #EAEAEA;
  border-radius: 1px;
  display: inline-block; }

.site-navigation__service {
  display: flex;
  justify-content: space-between;
  width: 100px; }

.site-navigation__signature {
  display: none; }

.site-navigation__indent {
  display: none; }

.site-navigation__indent::after {
  content: "";
  width: 2px;
  height: 32px;
  background: #EAEAEA;
  border-radius: 1px;
  display: inline-block; }

.site-navigation__repear:hover span {
  font-size: 16.5px; }

.site-navigation__repear:hover img {
  transform: scale(1.1); }

@media (min-width: 768px) {
  .site-navigation {
    padding-left: 24px;
    padding-right: 24px; }
  .site-navigation__burger {
    display: flex;
    align-items: center; }
  .site-navigation__burger::after {
    content: "";
    width: 2px;
    height: 32px;
    background: #EAEAEA;
    border-radius: 1px;
    display: inline-block;
    margin-left: 20px; }
  .site-navigation__logo::after {
    display: none; }
  .site-navigation__indent {
    display: flex;
    margin-left: 300px; } }

@media (min-width: 1440px) {
  .site-navigation {
    width: 1120px;
    margin: 0 auto;
    justify-content: flex-end;
    background-color: #F8F8F8; }
  .site-navigation__service {
    width: 325px;
    margin-right: 60px; }
  .site-navigation__signature {
    display: block;
    color: black;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px; }
  .site-navigation__repear {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 170px; }
  .site-navigation__li {
    margin-right: 15px; }
  .site-navigation__logo {
    display: none; }
  .site-navigation__indent {
    display: none; } }
