.sentence {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21;
  padding: 16px;
  background-color: #F2F2F2;
  display: block; }

@media (min-width: 768px) {
  .sentence {
    padding-left: 24px;
    padding-right: 24px; } }

@media (min-width: 1440px) {
  .sentence {
    width: 1056px;
    margin: 0 auto;
    padding-left: 32px;
    padding-right: 32px; } }
