body {
  position: relative; }

.mobile-nav {
  display: block;
  position: absolute;
  left: -308px;
  top: 0;
  height: 100%;
  width: 308px;
  background-color: #FFFFFF;
  z-index: 19;
  transition: transform 0.4s ease-in;
  box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2), -2px 0px 4px rgba(69, 79, 126, 0.02); }

.mobile-nav--active {
  transform: translateX(100%); }

.mobile-nav__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 24px; }

.mobile-nav__title .mobile-nav__exit:hover img {
  transform: scale(1.1); }

.mobile-nav__title .activeHover:hover {
  transform: scale(1.1); }

.mobile-nav__titlelink:nth-child(2) {
  flex-grow: 1;
  margin-left: 15px; }

.mobile-nav__link {
  display: block;
  padding: 16px 24px;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21; }

.mobile-nav__link:hover {
  background-color: #f5f5f5; }

.active::before {
  content: "";
  width: 4px;
  height: 37px;
  background: #41F6D7;
  border-radius: 0 6px 6px 0;
  display: inline-block; }

.active {
  display: flex;
  align-items: center; }

.active .mobile-nav__link {
  color: #7E7E82;
  background-color: white; }

.mobile-nav__Communications {
  display: flex;
  margin-left: 24px; }

@media (min-width: 1440px) {
  .mobile-nav {
    position: static;
    height: auto;
    box-shadow: 0 0 black; }
  .mobile-nav__exit {
    display: none; }
  .mobile-nav__titlelink:nth-child(2) {
    margin: 0; }
  .mobile-nav__link {
    padding: 14px 24px; }
  .mobile-nav__list {
    margin-bottom: 220px; } }
